@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
body {
  /* background-color: rgb(245, 172, 78); */
  /* background-color: rgb(128, 128, 128); */
  background-color: lightgray;
  padding: 20px;
  margin: 0;
}
h1,
h2,
p,
ul,
li {
  font-family: sans-serif;
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.header {
  background-color: #143ebc;
  padding: 0;
  box-shadow: 3px 7px 10px 0px rgba(45, 46, 67, 0.85);
  border-radius: 6px;
}
ul.header li a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
  border-radius: 6px;
}
.content {
  /* background-color: #fff; */
  padding: 20px;
  box-shadow: 2px 4px 8px 0px rgba(45, 46, 67, 0.85);
}
h1 {
  color: #29303d;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}

.active {
  background-color: #1f9bea;
}

.card {
  width: 26rem;
  border-radius: 6px;
}

.card-body {
  padding: 12px;
  border-radius: 6px;
}

.reactions-count {
  width: 26rem;
  padding: 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  font-size: 0.8rem;
}

.fa {
  font-size: x-large;
  margin-right: 8px;
}

.fa-thumbs-up {
  color: #339af0;
  font-size: 1rem;
}

form {
  width: 26rem;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
}

input[type="text"] {
  border-radius: 22px;
  background: #f2f3f5;
}

.table {
  width: 26rem;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
}

